.myiconmob {
  box-sizing: border-box;
  transition: transform 0.3s;
  width: 24px;
  height: 24px;
  transform: rotate(180deg);
  position: relative;
  /*&:hover {
    transform: rotate(0deg);
	}*/
  /*&:hover::before {
    content: '';
    display: block;
    width: 0px;
    height:0px;
    border-bottom: solid $lineWidth $color;
    position: absolute;
    bottom:$width /2 - $lineWidth/2;
    transform: rotate(90deg);
	}*/
}

.myiconmob::before {
  content: '';
  display: block;
  width: 24px;
  height: 0px;
  border-bottom: solid 2px #fff;
  position: absolute;
  bottom: 11px;
  transform: rotate(90deg);
  transition: width 0.3s;
}

.myiconmob::after {
  content: '';
  display: block;
  width: 24px;
  height: 0px;
  border-bottom: solid 2px #fff;
  position: absolute;
  bottom: 11px;
}

.myiconmob.minus {
  transform: rotate(0deg);
}

.myiconmob.minus::before {
  content: '';
  display: block;
  width: 0px;
  height: 0px;
  border-bottom: solid 2px #fff;
  position: absolute;
  bottom: 11px;
  transform: rotate(90deg);
}

